@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

.Name form {
  animation: fadeIn 2s linear;

  display: flex;
  flex-direction: column;
  font-size: 2rem;
}

.Name .text {
  font-weight: bold;
  margin-bottom: 0.5rem;
}

.Name input {
  all: unset;
  border-bottom: 2px solid #f1f1f1;
  text-align: center;
}
