.Weather {
  position: fixed;
  right: 0;
  top: 0;
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  margin: 1rem;
}

.Weather .temp {
  font-weight: 500;
  font-size: 2rem;
  display: flex;
  align-items: center;
  margin-bottom: 0.5rem;
}

.Weather .name {
  font-weight: 400;
  font-size: 1rem;
  margin-right: 0.2rem;
}

.Weather .location {
  font-size: 1.2rem;
}
